.top-container {
    width: 100%;
    position: relative;
}

.top-container img {
    width: 100%;
    height: 100vh;
}

.top-container .text-container {
    color: #fff;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
}

.top-container .text-container h1 {
    font-size: 45px;
}

.btn-container {
    margin-top: 30px;
}

.btn-container a {
    text-decoration: none;
    color: #000;
    background: #fff;
    border: none;
    padding: 5px 20px;
    font-size: 25px;
    margin-right: 15px;
    box-shadow: none;
}